import { Component, OnInit } from '@angular/core';
import { Base } from '../../../model/base';
import { Router } from '@angular/router';
import { TipoInspecao } from './../../../model/objetc/tipoinspecao';
import { TipoinspecaoService } from 'src/app/service/sigobra/tipoinspecao.service';
import { ItensTipoInspecaoService } from './../../../service/sigobra/itens-tipo-inspecao.service';
import { ItemTipoInspecao } from '../../../model/objetc/itemtipoinspecao';

@Component({
  selector: 'app-itenstipoinspecao',
  templateUrl: './itenstipoinspecao.page.html',
  styleUrls: ['./itenstipoinspecao.page.scss'],
})
export class ItenstipoinspecaoPage implements OnInit {

 tipoinspecao: TipoInspecao;
 itenstipoinspecoes: Array<ItemTipoInspecao>;
 itenstipoinspecao: ItemTipoInspecao = new ItemTipoInspecao();
 itiSelecionado: ItemTipoInspecao = new ItemTipoInspecao();
  
  indexInserir: number;

  constructor(private base: Base,
     private router: Router, private tis: TipoinspecaoService, private itis: ItensTipoInspecaoService) { }

  ngOnInit() {   
    if ((this.tis.tipoinspecao !== undefined) && (this.tis.tipoinspecao !== null)) {
      this.tipoinspecao = this.tis.tipoinspecao;
      if ((this.tipoinspecao.itenstipoinspecao === undefined) || (this.tipoinspecao.itenstipoinspecao === null)) {
        this.pesquisaTipoInspecao(this.tipoinspecao);
      }
    } else {
      this.tipoinspecao.itenstipoinspecao = new Array<ItemTipoInspecao>();
    }
  }

  pesquisaTipoInspecao(value: TipoInspecao) {
    this.base.present();
    return this.itis.bytipoinspecao(value).subscribe(
      data => {
        this.base.dismiss();
        this.itenstipoinspecoes = data as Array<ItemTipoInspecao>;
        this.tipoinspecao.itenstipoinspecao = this.itenstipoinspecoes;
      }, error => {
        this.base.dismiss();
        this.tipoinspecao.itenstipoinspecao = new Array<ItemTipoInspecao>();
  
      }
    );
  }


  onSelect(event: any) {
    if (event.item === undefined || event.item === null) {
      return;
    }
    

    

  }

  inserir() {
    this.itenstipoinspecao.ativo = true;
    if (this.indexInserir > 0) {
      this.itenstipoinspecoes.includes(this.itenstipoinspecao, this.indexInserir);
    } else {
      this.itenstipoinspecoes.push(this.itenstipoinspecao);
    }
    
    this.indexInserir = 0;
    this.itenstipoinspecao  = new ItemTipoInspecao();
  }

  editar(itens: ItemTipoInspecao, index: number = 0) {
    this.indexInserir = 1;
    this.itenstipoinspecao  = itens;
  }

  keyDownFunction(event: any) {

  }


  salvar() {

    this.tipoinspecao.itenstipoinspecao = this.itenstipoinspecoes;

    this.itis.salvarbytipoinspecao(this.tipoinspecao).subscribe(
      data => {
        this.base.router.navigate(['tipoinspecao-inserir']);
      }, error => {
        this.base.mensagemErro('Falha ao salvar esta ficha  :' + this.base.tratarErro(error));
      }
    );
  }

}
